.load {
    width: 200px;
    height: 200px;
    border: 3px red dashed;
    border-radius: 50%;
    margin: 150px auto;
    animation: rotate 5s infinite linear;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}